import { Component, OnInit, ViewChild } from '@angular/core';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { User } from 'src/models/User';
import { Team } from 'src/models/Team';
import { ModalController, IonInfiniteScroll } from '@ionic/angular';
import { ToastText } from 'src/commons/Constants';
import { Invitation } from 'src/models/Invitation';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-pending',
  templateUrl: './pending.page.html',
  styleUrls: ['./pending.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class PendingPage {

  
  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;
  
  

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "PendingPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  private team: Team;
  public invitations: Array<Invitation> = new Array<Invitation>();
  public invitationList: Array<Invitation> = new Array<Invitation>();
  private searchTerm: string;
  public imageConcatString = Utils.generateRandomString();

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(private api: BackendApiProvider,
    private utils: Utils,
    private pageDataService: PageDataService,
    private modalController: ModalController) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.team = this.pageDataService.getData('teamData');
    if (Utils.isVarInvalid(this.team)) {
      this.utils.simpleToastError(ToastText.SOMETHING_WENT_WRONG);
      this.modalController.dismiss();
    }
    this.getPendingUsers();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getPendingUsers() {
    this.api.getTeamPendingUsers(this.team.getId()).then((users: Array<Invitation>) => {
      this.imageConcatString = Utils.generateRandomString();
      this.invitations = users;
      this.invitationList = this.invitations;
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  approve(invite: Invitation) {
    this.api.approveUser(invite.getId()).then(() => {
      this.utils.simpleToast(ToastText.USER_ADDED);
      this.invitations.splice(this.invitations.indexOf(invite), 1);
      this.filter(this.searchTerm);
      this.pageDataService.listUserEvent.emit('pendingUserData');
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  disapprove(invite: Invitation) {
    this.api.disapproveUser(invite.getId()).then(() => {
      this.utils.simpleToast(ToastText.USER_REMOVED);
      this.invitations.splice(this.invitations.indexOf(invite), 1);
      this.filter(this.searchTerm);
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isStringInvalid(evt)) {
      this.invitationList = this.invitations;
      return;
    }
    const searchTerm = evt;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.invitationList = this.invitations;
      return;
    }
    this.invitationList = this.invitations.filter(n => n.getUserNome().toLowerCase().includes(searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  
  loadData(event) {
    setTimeout(() => {
      console.log('Done');
      event.target.complete();
      this.imageConcatString = Utils.generateRandomString();
      if (this.invitationList.length == 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }

}
